.header__btn--login {
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 100%;
  color: #ffffff;
  padding: 15px 40px;
  cursor: pointer;
  background: #1063b8;
  border-radius: 10px;
}

.header__btn--login:hover {
  background: #549de7;
}

.box--title {
  font-size: 20px;
  background-color: white;
}

.exitBtn {
  display: flex;
  position: absolute;
  top: 30px;
}

.exitBtn__title {
  color: #ffffff;
  width: 150px;
  font-weight: 300;
}

.modal--btn {
  position: absolute;
  cursor: pointer;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  color: rgb(186, 0, 0);
  cursor: pointer;
  border-bottom: 2px solid rgb(186, 0, 0);
  padding-bottom: 2px;
  top: 32px;
  right: 0;
}

.hide {
  opacity: 0;
  visibility: hidden;
}

.show {
  opacity: 1;
  visibility: visible;
}
