.nav__list {
  display: flex;
  column-gap: 50px;
  margin-right: 80px;
}

.nav__item {
  padding-bottom: 7px;
  display: flex;
  column-gap: 50px;
}

.nav__item:hover {
  padding-bottom: 5px;
  border-bottom: 2px solid #f2f2f2;
}

.nav__link {
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 100%;
  color: #f3f4f6;
  cursor: pointer;
}
