.Login {
  width: 100%;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(159, 173, 243);
}
.input {
  width: 80%;
  height: 40px;
  margin-top: 30px;
  outline: none;
  padding: 0 20px;
  background-color: transparent;
  border: 2px solid rgb(132, 103, 236);
}
.title {
  color: blueviolet;
}
.box {
  box-sizing: border-box;
  display: flex;
  width: 350px;
  align-items: center;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.299);
  padding: 10px;
  border-radius: 10px;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
}
.btn {
  width: 100px;
  height: 40px;
  margin-top: 30px;
  margin-bottom: 20px;
  border-radius: 10px;
  background-color: white;
  border: none;
  cursor: pointer;
}

.btn:hover {
  background-color: rgb(222, 216, 216);
}
