.hero {
  margin-top: 60px;
  text-align: center;
}
.hero__section {
  border-radius: 20px;
  display: inline-block;
  box-shadow: 12px 12px 2px 1px rgba(71, 71, 153, 0.281);
  padding-bottom: 30px;
}
.hero__section:hover {
  box-shadow: 12px 12px 2px 1px rgba(78, 78, 202, 0.753);
}

.hero__main--title {
  padding-bottom: 10px;
  font-style: italic;
  font-weight: 700;
  font-size: 24px;
  line-height: 100%;
  color: #000000;
  margin: 5px 0 15px;
}

.hero__main--img {
  border-radius: 20px;
  width: 370px;
  height: 285px;
  display: inline-block;
}
.hero__main--btn {
  margin-top: 15px;
  padding: 15px 50px;
  border-radius: 20px;
  cursor: pointer;
  background-color: rgb(132, 103, 236);
  margin-bottom: 30px;
  color: #ffffff;
}

.hero__main--btn:hover {
  background-color: rgb(41, 35, 70);
}
