.header__widget {
  text-align: center;
  margin-top: 65px;
  padding-bottom: 275px;
}
.header__widget--title {
  font-style: normal;
  font-weight: 400;
  font-size: 54px;
  line-height: 100%;
  color: #e5e5e5;
  margin-bottom: 30px;
}
.header__widget--text {
  font-style: normal;
  width: 800px;
  margin: 0 auto;
  font-weight: 400;
  font-size: 18px;
  line-height: 100%;
  color: #ffffff;
  margin-bottom: 80px;
}
.header__widget--input {
  background: linear-gradient(
    180deg,
    rgba(240, 139, 117, 0.43) 0%,
    rgba(34, 68, 103, 0.3) 100%
  );
  border: 3px solid #e5e5e5;
  backdrop-filter: blur(33px);
  border-radius: 15px;
  font-style: normal;
  font-weight: 400;
  width: 900px;
  height: 75px;
  padding: 0 50px;
  font-size: 24px;
  line-height: 100%;
  color: rgba(229, 229, 229, 0.45);
}
