.footer__navInfo {
  display: flex;
  justify-content: space-between;
}
.footer__nav--info {
  display: flex;
}
.footer__nav--desc {
  margin-left: 15px;
}
.footer__nav--title {
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 20px;
  display: flex;
  align-items: center;
  color: #1063b8;
  margin-bottom: 10px;
}
.footer__nav--text {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  display: flex;
  color: #ffffff;
  width: 270px;
}
.bx-rocket {
  font-size: 40px;
  color: white;
}
.bx-dollar-circle {
  font-size: 40px;
  color: white;
}
.bx-phone {
  font-size: 40px;
  color: white;
}
